/*----------------------------------------*/
/*  1.4 Default Button
/*----------------------------------------*/
button.li-btn {
    border: none;
    background: #fed700;
    color: #242424;
    font-size: 20px;
    width: 45px;
    border-radius: 0 2px 2px 0;
    height: 45px;
    line-height: 45px;
    position: absolute;
    right: -1px;
    top: -1px;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
}
button.li-btn:hover {
    background: #333333;
    color: #ffffff;
}
a.links,
a.links-2,
a.review-links {
    background: #fed700 none repeat scroll 0 0;
    color: #242424;
    display: inline-block;
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 700;
    height: 50px;
    line-height: 50px;
    position: relative;
    text-transform: uppercase;
    transition: all 0.3s ease-in-out 0s;
    width: 170px;
    text-align: center;
}
a.links:hover,
a.links-2:hover {
    background: #333333 none repeat scroll 0 0;
    color: #fff;
}
a.review-links {
    background: #242424;
    color: #ffffff;
    width: 205px;
    height: 40px;
    line-height: 40px;
}
a.review-links:hover {
    background: #fed700;
}
