    margin: 0 2px 5px;
}
.category-tags > ul > li:first-child {
    margin-top: 30px;
}
.category-tags > ul > li:hover {
    background: #242424;
}
.category-tags > ul > li:hover a {
    color: #ffffff;
}
/*----- paginatoin-area  css -----*/
.paginatoin-area {
    background: #ffffff none repeat scroll 0 0;
    border: 1px solid #ededed;
    border-radius: 3px;
    font-size: 14px;
    font-weight: 400;
    margin: 40px 0 0;
    padding: 9px;
}
.paginatoin-area p {
    margin: 0;
}
.pagination-box {
    text-align: right;
}
.pagination-box > li {
    display: inline-block;
}
.pagination-box > li.active a {
    color: #fed700;
}
.pagination-box > li a {
