}
.product-details-view-content .product-desc p {
    margin-bottom: 20px;
}
.single-add-to-cart {
    float: left;
    width: 100%;
}
.product-additional-info {
    float: left;
    width: 100%;
}
.product-additional-info .wishlist-btn i {
    padding-right: 10px;
}
.product-social-sharing > ul > li {
    display: inline-block;
