}
.product-details-manufacturer p {
    margin: 0;
}
.product-details-manufacturer p span {
    color: #232323;
    font-weight: 700;
}
.product-details-comment-block .comment-review span {
    display: inline-block;
    font-size: 14px;
    font-weight: 600;
    color: #6c6c6c;
}
.comment-author-infos span {
    color: #333;
    font-weight: 600;
}
.product-details-comment-block .comment-review .rating {
    display: inline-block;
    padding-left: 10px;
    font-size: 18px;
}
.comment-author-infos em {
    display: block;
    color: #adadad;
}
.comment-details {
    overflow: hidden;
    margin-top: 25px;
}
.comment-details h4 {
    font-weight: 600;
    text-transform: capitalize;
    font-size: 14px;
    color: #6c6c6c;
}
/*Review Content*/
.review-content {
    position: relative;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.2);
    border-radius: .3rem;
    outline: 0;
}
.review-body {
    position: relative;
    padding: 15px;
}
#li-comment-wrap {
    overflow: hidden;
    color: #333;
    text-align: left;
}
.review-page-title {
    padding: 10px;
    font-size: 13px;
    color: #ffffff;
    text-transform: uppercase;
    background: #333;
}
.li-review-product {
    padding: 15px;
}
.li-review-product img {
    border: 1px solid #ebebeb;
}
.li-review-product-desc {
    line-height: 18px;
    color: #6f6f6f;
}
.li-product-name {
    padding: 10px 0 5px;
    font-size: 13px;
    color: #000;
}
.feedback-area .br-theme-fontawesome-stars .br-widget a {
    font-size: 14px;
}
.feedback-area .br-theme-fontawesome-stars .br-widget a.br-active:after {
    color: #fed700;
}
.feedback-area .br-theme-fontawesome-stars .br-widget a.br-selected::after {
    color: #fed700;
}
.feedback-area textarea, .feedback-area input {
    background: #fff;
    border: 1px solid #ebebeb;
    font-size: 14px;
}
.feedback-btn {
    position: relative;
}
.feedback-btn .close,
.feedback-btn a {
    background: #242424;
    color: #fff !important;
    width: 80px;
    font-size: 14px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    left: 110px;
    right: auto;
    top: 0;
    display: block;
    transition: all 0.3s ease-in-out;
}
.feedback-btn .close:hover,
.feedback-btn a:hover {
    background: #fed700;
}
