/* 6.13 Single Product Carousel Page
/*----------------------------------------*/
.sp-carousel-page .single-product {
    border: 1px solid #e1e1e1;
}
.sp-carousel-content {
    text-align: center;
}
.sp-carousel-content .single-add-to-cart {
    width: 100%;
}
.sp-carousel-content .quantity {
