}
.sp-carousel-content .cart-quantity label {
    display: inline-block;
    margin-right: 10px;
}
.sp-carousel-content .cart-plus-minus {
    float: unset;
    display: inline-block;
}
.sp-carousel-content .cart-quantity {
    margin-top: 5px;
}
.sp-carousel-content .product-social-sharing {
    padding-top: 30px;
}
