/* 6.15 Single Product Group Page
/*----------------------------------------*/
.sp-group-content .product-quantity label {
    padding: 13px 0 0;
    margin: 0;
}
.sp-group-content .product-desc {
    border-bottom: none;
}
.sp-group-content .product-variants {
    padding-top: 10px;
}
